/* great-vibes-regular - latin */
@font-face {
  font-family: "Great Vibes";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/great-vibes-v5-latin-regular.eot"); /* IE9 Compat Modes */
  src: local("Great Vibes"), local("GreatVibes-Regular"),
    url("../fonts/great-vibes-v5-latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/great-vibes-v5-latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("../fonts/great-vibes-v5-latin-regular.woff") format("woff"),
    /* Modern Browsers */ url("../fonts/great-vibes-v5-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/great-vibes-v5-latin-regular.svg#GreatVibes") format("svg"); /* Legacy iOS */
}
/* lobster-regular - latin */
@font-face {
  font-family: "Lobster";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/lobster-v21-latin-regular.eot"); /* IE9 Compat Modes */
  src: local("Lobster Regular"), local("Lobster-Regular"),
    url("../fonts/lobster-v21-latin-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */ url("../fonts/lobster-v21-latin-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */ url("../fonts/lobster-v21-latin-regular.woff")
      format("woff"),
    /* Modern Browsers */ url("../fonts/lobster-v21-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("../fonts/lobster-v21-latin-regular.svg#Lobster") format("svg"); /* Legacy iOS */
}
.App {
  text-align: center;
}

.App-header {
  background-color: antiquewhite;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: blue;
}

.flex-container {
  display: flex;
  border: 1px solid #61dafb;
}

.flex-row {
  flex-direction: row;
  justify-content: space-around;
}

